import React, { useState } from 'react';
import CategoriesLink from '../CategoriesLink';
import { Turn as Hamburger } from 'hamburger-react';
import bancos from '../../icons/bancos.png';
import pesos from '../../icons/pesos.png';
import acessorios from '../../icons/acessorios.png';
import Offcanvas from 'react-bootstrap/Offcanvas';
import './style.css'

export default function Header() {

    const [show, setShow] = useState(false);
    const handleClose = () => setShow(false);
    const handleShow = () => setShow(true);

    return (
      <div className="header-offcanvas-container">
        <Hamburger color="#36731A" toggled={ show } toggle={ handleShow } />
        <Offcanvas scroll placement="end" className="offc-container" show={show} onHide={handleClose}>
          <Offcanvas.Header closeButton>
          </Offcanvas.Header>
          <Offcanvas.Body className="offc">
            <ul className="offc-ul">
              <CategoriesLink title="bancos" id="000001" close={ handleClose } icon={ bancos } />
              <CategoriesLink title="pesos" id="000002" close={ handleClose } icon={ pesos } />
              <CategoriesLink title="acessórios" id="000004" close={ handleClose } icon={ acessorios } />
            </ul>
          </Offcanvas.Body>
        </Offcanvas>
      </div>
    );
  }
