export const fetchAllProducts = async () => {
    const products = await fetch('https://catalogue-back-end.herokuapp.com/products');
    const productsJSON = await products.json();
    return productsJSON;
}

export const fetchProductsByCategoryId = async (categoryId) => {
    const products = await fetch(`https://catalogue-back-end.herokuapp.com/products/category/${categoryId}`);
    const productsJSON = await products.json();
    return productsJSON;
};

export const fetchCategoryById = async (categoryId) => {
    const category = await fetch(`https://catalogue-back-end.herokuapp.com/categories/${categoryId}`);
    const categoryJSON = await category.json();
    return categoryJSON;
};

export const fetchProductById = async (id) => {
    const product = await fetch(`https://catalogue-back-end.herokuapp.com/products/${id}`);
    const productJSON = await product.json();
    return productJSON;
};

export const fetchAllCategories = async () => {
    const categories = await fetch('https://catalogue-back-end.herokuapp.com/categories');
    const categoriesJSON = await categories.json();
    return categoriesJSON;
};

export const fetchImagesByProductId = async (id) => {
    const images = await fetch(`https://catalogue-back-end.herokuapp.com/images/${id}`);
    const imagesJSON = await images.json();
    return imagesJSON;
};

export const fetchProductsByQuery = async (query) => {
    const products = await fetch(`https://catalogue-back-end.herokuapp.com/products/search?q=${query}`);
    const productsJSON = await products.json();
    return productsJSON;
};